import {
  Box,
  Container,
  Stack,
  Tab,
  TabList,
  TabPanel,
  TabPanels,
  Tabs,
} from "@chakra-ui/react";
import React, { useState } from "react";
import CustomSpinner from "../components/loading/CustomSpinner";
import AppointmentsFilter from "../components/filters/AppointmentsFilter";
import AppointmentsGrid from "../components/appointments/AppointmentsGrid";
import { filterAppointments } from "../utils/functions";
import { useGetAppointmentsQuery } from "../store/api-slice";

function Dashboard() {
  const [searchTerm, setSearchTerm] = useState("");
  const [filterField, setFilterFields] = useState({
    owner: "",
    visa: "",
    status: "",
  });

  const { data, isSuccess, isFetching } = useGetAppointmentsQuery(filterField);

  return (
    <Container maxW="6xl">
      <Box my={20}>
        {!isSuccess ? (
          <CustomSpinner />
        ) : (
          <Stack spacing={5}>
            <AppointmentsFilter
              searchTerm={searchTerm}
              setSearchTerm={setSearchTerm}
              filterField={filterField}
              setFilterFields={setFilterFields}
              attributes={data.attributes}
            />

            <Tabs variant="enclosed">
              <TabList>
                <Tab>Created</Tab>
                <Tab>Assigned</Tab>
              </TabList>
              <TabPanels>
                <TabPanel>
                  <AppointmentsGrid
                    permission="createdBy"
                    appointments={filterAppointments(
                      data.payload.createdAppointments,
                      "passportNumber",
                      searchTerm
                    )}
                    attributes={data.attributes}
                    isFetching={isFetching}
                  />
                </TabPanel>
                <TabPanel>
                  <AppointmentsGrid
                    permission="assigned"
                    appointments={filterAppointments(
                      data.payload.assignedAppointments,
                      "passportNumber",
                      searchTerm
                    )}
                    attributes={data.attributes}
                    isFetching={isFetching}
                  />
                </TabPanel>
              </TabPanels>
            </Tabs>
          </Stack>
        )}
      </Box>
    </Container>
  );
}

export default Dashboard;
